import React from 'react'
import {Link} from 'react-router-dom';

export default function Socials() {
    return (
        <div className="socials">
            <Link to="#"><img alt="github icon" src="https://icons-for-free.com/iconfiles/png/512/code+collaboration+github+network+round+social+icon-1320086084536018107.png"></img></Link>
            <Link to="#"><img alt="linkedIN icon" src="https://image.flaticon.com/icons/png/512/174/174857.png"></img></Link>
        </div>
    )
}
