import React, { Component } from 'react';
import {Link} from 'react-router-dom';

export default class Main extends Component {
    constructor(props){  
        super(props)
        this.state = {
            greetingData:'G\'Day, ',
            shortDescriptionData:'my name is Abdelkarim CHAMLAL and I\'m a software developer who likes to build ideas and automate tasks.',
            greeting:[],
            shortDescription:[],
            space: "\u00A0",
            faddingTextDelay:5,
            profileIMG:'https://t3.ftcdn.net/jpg/03/28/19/46/360_F_328194664_RKSHvMLgHphnD1nwQYb4QKcNeEApJmqa.jpg'
        }
        for(let i= 0 ; i < this.state.greetingData.length ; i++){this.state.greeting.push(this.state.greetingData.substring(i,i+1))}
        for(let i= 0 ; i < this.state.shortDescriptionData.length ; i++){this.state.shortDescription.push(this.state.shortDescriptionData.substring(i,i+1))}
   }  

    
    render() {
        
        const greetingOutput = this.state.greeting.map((char)=>{
            return (
                <span key={Math.random()*1000} style={{
                    animationName:"fadeInAnimation",
                    animationDuration:Math.random()*this.state.faddingTextDelay+1+"s"
                }}>
                    {char}
                </span>
            )
        });

        const descriptionOutput = this.state.shortDescription.map((char)=>{
            return (
                <span key={Math.random()*1000} style={{
                    animationName:"fadeInAnimation",
                    animationDuration:Math.random()*this.state.faddingTextDelay+1+"s"
                }}>
                    {char}
                </span>
            )
        });


        return (
            <div className="main">
                <div className="portfolioIMG">
                    <img alt="profile" src={this.state.profileIMG}/>
                </div>

                <div className="mainContent">
                    <div className="description">
                        <mark>
                            {greetingOutput}
                        </mark>

                        {descriptionOutput}
                    </div>
                
                    <div className="mainBTN">
                        <Link to="/resume">MORE ABOUT ME</Link>
                        <Link to="/contact">DOWNLOAD CV</Link>
                    </div>
                </div>

            </div>
        )
    }
}
